.wrap_login { display: flex; width: 100%; height: 100%; justify-content: center; align-items: center; background-color: #F5F4FC; column-gap: 60px;
	.logo_woori { height: 20px; margin-bottom: 20px; background: url("../../../assets/images/logo_wb_login.svg") left center no-repeat; text-indent: -99999rem;}
	> div { width: 350px; min-height: 424px;}
}

.form_login { padding-top: 20px;
	h2 { color: #000000; font-size: 32px; font-weight: 700; margin-bottom: 30px;}
	.form { display: flex; row-gap: 30px; flex-direction: column;
		.inp_box { border-radius: 8px; -webkit-border-radius: 8px; -moz-border-radius: 8px; -ms-border-radius: 8px; -o-border-radius: 8px; overflow: hidden;
			.line { padding: 0; height: 56px; --padding-start: 0; --padding-end: 0; border: none; }
			input { height: 100% !important; padding-left: 20px !important; border-radius: 8px; -webkit-border-radius: 8px; -moz-border-radius: 8px; -ms-border-radius: 8px; -o-border-radius: 8px; }
			
		}
	}
    .option { display: flex; justify-content: flex-end; margin-top: -8px;
        ion-item { --min-height: 24px; font-weight: 300;}
    }
    
}

.btn_login { margin-top: 10px; height: 48px; font-size: 18px; font-weight: 500;}
.btn_forgot { color: #999999; font-size: 14px; font-weight: 300; --background: transparent; --padding-start: 0; --padding-end: 0; text-decoration: underline;}

.form_login .verify_email { row-gap: 40px;
	> div { display: flex; flex-direction: column;
		.inp_box { margin-bottom: 20px;}
		ion-button { margin: 0;}
		ion-button + ion-button { margin-top: 12px;}
	}
}

.inp_box .time { right: 20px;}
